export interface IContactUsDto {
    contactUsTypeId: number | undefined;
    details: string | undefined;
    attachmentUrl: string | undefined;
    id: number | undefined;
}


export class ContactUsDto implements IContactUsDto {
    contactUsTypeId!: number | undefined;
    details!: string | undefined;
    attachmentUrl!: string | undefined;
    id!: number | undefined;

    constructor(data?: IContactUsDto) {

            this.attachmentUrl = data.attachmentUrl;
            this.contactUsTypeId = data.contactUsTypeId;
            this.details = data.details;
            this.id = data.id;
        
    }


}