import { Component, OnInit } from '@angular/core';
import { ContactUsService } from 'src/app/services/ContactUs/ContactUs';
import { AlertController } from '@ionic/angular';
import { ContactUsDto, IContactUsDto } from 'src/app/models/ContactUsDto';
import { LoadingService } from 'src/app/services/loading/loading.service';

@Component({
  selector: 'app-get-in-touch',
  templateUrl: './get-in-touch.page.html',
  styleUrls: ['./get-in-touch.page.scss'],
})
export class GetInTouchPage implements OnInit {

  public form = [
    { val: 'Pepperoni', isChecked: true },
    { val: 'Sausage', isChecked: false },
    { val: 'Mushroom', isChecked: false }
  ];

  radio;

  contactUs : IContactUsDto;

  //contact us form 
  Details;

  attachmentUrl;
  fileName : string ;

  constructor(
    private _inTouch     : ContactUsService,
    private alertctrl    : AlertController,
    private _loading     : LoadingService
  ) { }

  ngOnInit() {
  }

  
  getInTouch(){

    this._loading.present();

    console.log(this.radio)

    this.contactUs =
      {
        "contactUsTypeId": this.radio,
        "details":  this.Details,
        "attachmentUrl": "",
        "id": undefined
      }
    

    let contact = new ContactUsDto (this.contactUs);

    this._inTouch.create(contact).subscribe(data => {
      console.log(data)
        setTimeout(() => {
          this._loading.dismiss().then(()=>{
            this.presentAlert("Your complaint has been sent successfully");
          })
        }, 1500);
      

    },error =>{
      this._loading.dismiss();
      alert(error)
    })
  }

  
  async presentAlert(message : string) {
    const alert = await this.alertctrl.create({
      header: 'Request successful',
      message: message,
      mode :'ios',
      buttons: [{
        text:'ok',
        handler :()=>{
          this.radio = 0;
          this.Details ='';
        }
      }]
    });
    alert.present();
  }

  fileAttach(files) {

    console.log(files)
    this.fileName = files[0].name;

    if (files.length === 0)
      return;
  
    var reader = new FileReader();
    reader.readAsDataURL(files[0]); 
    reader.onload = (_event) => { 
      this.attachmentUrl = reader.result; 
      console.log( this.attachmentUrl)
    }
  }
   
}
